import React, { useState, useEffect, useCallback } from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";
import { DataGrid, GridToolbar } from "@mui/x-data-grid";
import style from "./style.css";
import { Button } from "@mui/material";
import Dialog from "@mui/material/Dialog";
import Box from "@mui/material/Box";
import DialogContent from "@mui/material/DialogContent";
import CircularProgress from "@mui/material/CircularProgress";

function RaisePrTasksBody(props) {
  let [openDialog, setOpenDialog] = useState(false);
  let [items, setItems] = useState([]);
  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });

  useEffect(() => {
    setOpenDialog(true);
    axiosInstance
      .get("projonline/gettasks/toBeRaisedPr", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setItems(res.data.tasks);
        // console.log(res.data.tasks)
        setOpenDialog(false);
        return;
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const handleClick = (event, cellValues) => {
    // console.log(cellValues)
    props.history.push({ pathname: "/raisePrSpecificTask", data: cellValues });
  };

  const columns = [
    { field: "taskRef", headerName: "Task ID", width: 250 },
    { field: "siteID", headerName: "Site ID", width: 130 },
    {
      field: "companyName",
      headerName: "Assigned Subcon / Vender",
      width: 290,
      valueGetter: (params) => params.row.assignedSubcon.companyName,
    },
    {
      field: "assignedMobitelOfficer",
      headerName: "Assinged Officer",
      width: 250,
      valueGetter: (params) => params.row.assignedMobitelOfficer.name,
    },
    {
      field: "taskRaisedDate",
      headerName: "Initiated date",
      width: 180,
      valueGetter: (params) =>
        params.row.taskHistory
          .find((x) => x.taskStatus === "Subcon allocated")
          .performedDate.split("T")[0],
    },
    // For add Capex/Opex amounts and total cost
    {
      field: "CapexPrAmount",
      headerName: "Capex Amount",
      width: 190,
      align: "right",
      valueGetter: (params) => {
        const boqs = params.row.boqs;
        const value1 =
          boqs.length > 0 ? boqs[boqs.length - 1].CapexPrAmount || 0 : 0;
        const value = value1 || "Not Defined";
        return value.toLocaleString("en-US", {
          minimumFractionDigits: 2,
          maximumFractionDigits: 2,
        });
      },
    },
    {
      field: "OpexPrAmount",
      headerName: "Opex Amount",
      width: 190,
      align: "right",
      valueGetter: (params) => {
        const boqs = params.row.boqs;
        const value1 =
          boqs.length > 0 ? boqs[boqs.length - 1].OpexPrAmount || 0 : 0;
        const value = value1 || "Not Defined";
        return value.toLocaleString("en-US", {
          minimumFractionDigits: 2,
          maximumFractionDigits: 2,
        });
      },
    },
    {
      field: "totalCost",
      headerName: "Total Value",
      width: 190,
      align: "right",
      valueGetter: (params) => {
        const value =
          params.row.boqs[params.row.boqs.length - 1][params.field] || 0;
        return value.toLocaleString("en-US", {
          minimumFractionDigits: 2,
          maximumFractionDigits: 2,
        });
      },
    },

    {
      field: "To Raise PR",
      width: 150,
      renderCell: (cellValues) => {
        return (
          <Button
            variant="contained"
            color="primary"
            onClick={(event) => {
              handleClick(event, cellValues);
            }}
          >
            To Raise PR
          </Button>
        );
      },
    },
  ];

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpenDialog(false);
  };

  return (
    <div>
      {/* Content Wrapper. Contains page content */}
      <div className="content-wrapper">
        <div className="content-header">
          <section className="content-header">
            <div className="container-fluid">
              <div className="row mb-2">
                <div className="col-sm-6">
                  <h1>All Tasks to Raise PRs</h1>
                </div>
              </div>
            </div>
            {/* /.container-fluid */}
          </section>
        </div>

        <div style={{ height: 610, width: "100%" }}>
          <DataGrid
            getRowId={(item) => item._id}
            rows={items}
            columns={columns}
            pageSize={15}
            rowsPerPageOptions={[15]}
            rowHeight={30}
            headerHeight={40}
            components={{ Toolbar: GridToolbar }}
          />
        </div>
      </div>

      <Dialog
        open={openDialog}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default withRouter(RaisePrTasksBody);
